// Types
interface Domain {
  id: string
  domainName: string
  tld: string
  status: 'pending' | 'active' | 'expired' | 'suspended' | 'transferring'
  registrationDate: string
  expirationDate: string
  autoRenew: boolean
  emailEnabled: boolean
  emailAccountsCount: number
  maxEmailAccounts: number
  spfConfigured: boolean
  dkimConfigured: boolean
  dmarcConfigured: boolean
  verificationStatus: 'pending' | 'verified' | 'failed'
}

interface EmailAccount {
  id: string
  email: string
  displayName: string
  domainId: string
  domainName: string
  status: 'pending' | 'active' | 'suspended' | 'deleted'
  storageQuota: number
  storageUsed: number
  sendLimitDaily: number
  sentToday: number
  warmupEnabled: boolean
  warmupStatus: string | null
  warmupProgress: number
  reputationScore: number
  campaignEnabled: boolean
  lastLoginAt: string | null
  lastSentAt: string | null
  createdAt: string
}

interface DnsRecord {
  id: string
  type: 'A' | 'AAAA' | 'CNAME' | 'MX' | 'TXT' | 'NS' | 'SRV'
  name: string
  value: string
  ttl: number
  priority?: number
  isSystem: boolean
}

interface DomainPricing {
  tld: string
  registrationPrice: number
  renewalPrice: number
  isPopular: boolean
  isFeatured: boolean
}

interface EmailPlan {
  id: string
  name: string
  description: string
  maxAccounts: number
  storagePerAccount: number
  sendLimitDaily: number
  monthlyPrice: number
  yearlyPrice: number
  warmupIncluded: boolean
  isPopular: boolean
}

