// Helper Functions
const getStatusColor = (status: string) => {
  switch (status) {
    case 'active': case 'verified': case 'ready': return 'bg-emerald-100 text-emerald-700 border-emerald-200'
    case 'warming': return 'bg-blue-100 text-blue-700 border-blue-200'
    case 'pending': return 'bg-amber-100 text-amber-700 border-amber-200'
    case 'expired': case 'suspended': case 'failed': case 'error': return 'bg-red-100 text-red-700 border-red-200'
    default: return 'bg-gray-100 text-gray-700 border-gray-200'
  }
}

const getReputationColor = (score: number) => {
  if (score >= 80) return 'text-emerald-600'
  if (score >= 60) return 'text-amber-600'
  if (score > 0) return 'text-red-600'
  return 'text-gray-400'
}

const formatStorage = (mb: number) => {
  if (mb >= 1024) return `${(mb / 1024).toFixed(1)} GB`
  return `${mb} MB`
}

const formatDate = (dateString: string | null): string => {
  if (!dateString) return 'Never'
  if (typeof window === 'undefined') return dateString
  try {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    })
  } catch {
    return dateString
  }
}

// Main Component
export default function EmailInfrastructurePage() {
  const [activeTab, setActiveTab] = useState('overview')
  const [domains, setDomains] = useState<Domain[]>(mockDomains)
  const [emailAccounts, setEmailAccounts] = useState<EmailAccount[]>(mockEmailAccounts)
  const [selectedDomain, setSelectedDomain] = useState<Domain | null>(null)
  const [selectedEmail, setSelectedEmail] = useState<EmailAccount | null>(null)
  const [showDomainModal, setShowDomainModal] = useState(false)
  const [showEmailModal, setShowEmailModal] = useState(false)
  const [showSearchDomainModal, setShowSearchDomainModal] = useState(false)
  const [showCreateEmailModal, setShowCreateEmailModal] = useState(false)
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
  }, [])

  // Calculate stats
  const stats = useMemo(() => {
    const activeDomains = domains.filter(d => d.status === 'active').length
    const totalEmails = emailAccounts.length
    const warmedEmails = emailAccounts.filter(e => e.warmupStatus === 'ready').length
    const warmingEmails = emailAccounts.filter(e => e.warmupStatus === 'warming').length
    const avgReputation = emailAccounts.filter(e => e.reputationScore > 0).length > 0
      ? Math.round(emailAccounts.filter(e => e.reputationScore > 0).reduce((sum, e) => sum + e.reputationScore, 0) / emailAccounts.filter(e => e.reputationScore > 0).length)
      : 0
    const sentToday = emailAccounts.reduce((sum, e) => sum + e.sentToday, 0)
    const expiringDomains = domains.filter(d => {
      const expDate = new Date(d.expirationDate)
      const thirtyDaysFromNow = new Date()
      thirtyDaysFromNow.setDate(thirtyDaysFromNow.getDate() + 30)
      return expDate <= thirtyDaysFromNow && d.status === 'active'
    }).length

    return {
      totalDomains: domains.length,
      activeDomains,
      totalEmails,
      warmedEmails,
      warmingEmails,
      avgReputation,
      sentToday,
      expiringDomains,
    }
  }, [domains, emailAccounts])

  return (
    <div className="space-y-6 -m-4 sm:-m-6 lg:-m-8">
      {/* Page Header */}
      <div className="flex-none bg-white border-b border-gray-200 px-4 sm:px-6 lg:px-8 pt-4 sm:pt-6 lg:pt-8 pb-0">
        <PageHeader
          title="Email Infrastructure"
          description="Manage domains, email accounts, and warmup integration"
          actions={
            <div className="flex items-center gap-3">
              <button 
                onClick={() => setShowSearchDomainModal(true)}
                className="px-4 py-2 text-gray-600 border border-gray-300 rounded-lg hover:bg-gray-50 text-sm flex items-center gap-2"
              >
                <span>üîç</span>
                Search Domain
              </button>
              <button 
                onClick={() => setShowCreateEmailModal(true)}
                className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm flex items-center gap-2"
              >
                <span>‚ûï</span>
                Create Email
              </button>
            </div>
          }
        />

        {/* Tab Navigation */}
        <nav className="-mb-px flex space-x-6 overflow-x-auto" aria-label="Tabs">
          {INFRASTRUCTURE_TABS.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === tab.id
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {tab.label}
            </button>
          ))}
        </nav>
      </div>

      {/* Tab Content */}
      <div className="px-4 sm:px-6 lg:px-8 pb-8">
        {activeTab === 'overview' && (
          <OverviewTab 
            stats={stats} 
            domains={domains} 
            emailAccounts={emailAccounts}
            onViewDomain={(d) => { setSelectedDomain(d); setShowDomainModal(true); }}
            onViewEmail={(e) => { setSelectedEmail(e); setShowEmailModal(true); }}
            isClient={isClient}
          />
        )}
        {activeTab === 'domains' && (
          <DomainsTab 
            domains={domains}
            onViewDomain={(d) => { setSelectedDomain(d); setShowDomainModal(true); }}
            onSearchDomain={() => setShowSearchDomainModal(true)}
            isClient={isClient}
          />
        )}
        {activeTab === 'email-accounts' && (
          <EmailAccountsTab 
            emailAccounts={emailAccounts}
            domains={domains}
            onViewEmail={(e) => { setSelectedEmail(e); setShowEmailModal(true); }}
            onCreateEmail={() => setShowCreateEmailModal(true)}
            isClient={isClient}
          />
        )}
        {activeTab === 'dns' && (
          <DnsTab domains={domains} />
        )}
        {activeTab === 'warmup' && (
          <WarmupIntegrationTab 
            emailAccounts={emailAccounts}
            onViewEmail={(e) => { setSelectedEmail(e); setShowEmailModal(true); }}
          />
        )}
        {activeTab === 'billing' && (
          <BillingTab 
            domains={domains}
            emailPlans={mockEmailPlans}
            domainPricing={mockDomainPricing}
          />
        )}
        {activeTab === 'settings' && (
          <SettingsTab />
        )}
      </div>

      {/* Modals */}
      {showSearchDomainModal && (
        <SearchDomainModal 
          pricing={mockDomainPricing}
          onClose={() => setShowSearchDomainModal(false)} 
        />
      )}
      {showCreateEmailModal && (
        <CreateEmailModal 
          domains={domains.filter(d => d.status === 'active' && d.emailEnabled)}
          onClose={() => setShowCreateEmailModal(false)} 
        />
      )}
      {showDomainModal && selectedDomain && (
        <DomainDetailModal 
          domain={selectedDomain}
          emailAccounts={emailAccounts.filter(e => e.domainId === selectedDomain.id)}
          onClose={() => setShowDomainModal(false)}
          isClient={isClient}
        />
      )}
      {showEmailModal && selectedEmail && (
        <EmailDetailModal 
          email={selectedEmail}
          onClose={() => setShowEmailModal(false)}
          isClient={isClient}
        />
      )}
    </div>
  )
}

