// DNS TAB
// ==============================================
function DnsTab({ domains }: { domains: Domain[] }) {
  const [selectedDomainId, setSelectedDomainId] = useState<string>(domains[0]?.id || '')
  const selectedDomain = domains.find(d => d.id === selectedDomainId)

  const mockDnsRecords: DnsRecord[] = [
    { id: 'dns-001', type: 'A', name: '@', value: '192.0.2.1', ttl: 3600, isSystem: false },
    { id: 'dns-002', type: 'CNAME', name: 'www', value: '@', ttl: 3600, isSystem: false },
    { id: 'dns-003', type: 'MX', name: '@', value: 'mail.domain.com', ttl: 3600, priority: 10, isSystem: true },
    { id: 'dns-004', type: 'TXT', name: '@', value: 'v=spf1 include:_spf.google.com ~all', ttl: 3600, isSystem: true },
    { id: 'dns-005', type: 'TXT', name: '_dmarc', value: 'v=DMARC1; p=quarantine; rua=mailto:dmarc@domain.com', ttl: 3600, isSystem: true },
  ]

  return (
    <div className="space-y-6">
      {/* Domain Selector */}
      <div className="flex items-center gap-4">
        <label className="text-sm font-medium text-gray-700">Select Domain:</label>
        <select
          value={selectedDomainId}
          onChange={(e) => setSelectedDomainId(e.target.value)}
          className="px-4 py-2 border border-gray-300 rounded-lg"
        >
          {domains.map(d => (
            <option key={d.id} value={d.id}>{d.domainName}</option>
          ))}
        </select>
      </div>

      {selectedDomain && (
        <>
          {/* DNS Health */}
          <div className="bg-white rounded-xl border border-gray-200 p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Email Deliverability DNS</h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className={`p-4 rounded-lg border-2 ${selectedDomain.spfConfigured ? 'border-emerald-200 bg-emerald-50' : 'border-amber-200 bg-amber-50'}`}>
                <div className="flex items-center justify-between mb-2">
                  <span className="font-medium">SPF Record</span>
                  <span className={selectedDomain.spfConfigured ? 'text-emerald-600' : 'text-amber-600'}>
                    {selectedDomain.spfConfigured ? '‚úì Configured' : '‚ö† Missing'}
                  </span>
                </div>
                <p className="text-xs text-gray-600">
                  Specifies which mail servers can send email on behalf of your domain.
                </p>
              </div>
              <div className={`p-4 rounded-lg border-2 ${selectedDomain.dkimConfigured ? 'border-emerald-200 bg-emerald-50' : 'border-amber-200 bg-amber-50'}`}>
                <div className="flex items-center justify-between mb-2">
                  <span className="font-medium">DKIM Record</span>
                  <span className={selectedDomain.dkimConfigured ? 'text-emerald-600' : 'text-amber-600'}>
                    {selectedDomain.dkimConfigured ? '‚úì Configured' : '‚ö† Missing'}
                  </span>
                </div>
                <p className="text-xs text-gray-600">
                  Adds a digital signature to verify your emails are authentic.
                </p>
              </div>
              <div className={`p-4 rounded-lg border-2 ${selectedDomain.dmarcConfigured ? 'border-emerald-200 bg-emerald-50' : 'border-amber-200 bg-amber-50'}`}>
                <div className="flex items-center justify-between mb-2">
                  <span className="font-medium">DMARC Record</span>
                  <span className={selectedDomain.dmarcConfigured ? 'text-emerald-600' : 'text-amber-600'}>
                    {selectedDomain.dmarcConfigured ? '‚úì Configured' : '‚ö† Missing'}
                  </span>
                </div>
                <p className="text-xs text-gray-600">
                  Instructs receiving servers how to handle emails that fail authentication.
                </p>
              </div>
            </div>
            {(!selectedDomain.spfConfigured || !selectedDomain.dkimConfigured || !selectedDomain.dmarcConfigured) && (
              <button className="mt-4 px-4 py-2 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700">
                Auto-Configure Missing Records
              </button>
            )}
          </div>

          {/* DNS Records Table */}
          <div className="bg-white rounded-xl border border-gray-200 overflow-hidden">
            <div className="px-6 py-4 border-b border-gray-200 flex items-center justify-between">
              <h3 className="font-semibold text-gray-900">DNS Records</h3>
              <button className="px-4 py-2 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700">
                + Add Record
              </button>
            </div>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Type</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Name</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Value</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">TTL</th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {mockDnsRecords.map((record) => (
                    <tr key={record.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4">
                        <span className="text-xs px-2 py-1 bg-gray-100 text-gray-700 rounded font-mono">
                          {record.type}
                        </span>
                      </td>
                      <td className="px-6 py-4 font-mono text-sm">{record.name}</td>
                      <td className="px-6 py-4">
                        <div className="font-mono text-sm text-gray-600 max-w-md truncate" title={record.value}>
                          {record.priority && <span className="text-gray-400 mr-2">{record.priority}</span>}
                          {record.value}
                        </div>
                      </td>
                      <td className="px-6 py-4 text-sm text-gray-600">{record.ttl}s</td>
                      <td className="px-6 py-4">
                        {record.isSystem ? (
                          <span className="text-xs text-gray-400">System</span>
                        ) : (
                          <div className="flex gap-2">
                            <button className="text-blue-600 hover:text-blue-700 text-sm">Edit</button>
                            <button className="text-red-600 hover:text-red-700 text-sm">Delete</button>
                          </div>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </>
      )}
    </div>
  )
}

